import React, { useEffect, useState } from 'react';
import { makeStyles } from "@material-ui/core/styles";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import { Link, useParams } from 'react-router-dom/cjs/react-router-dom.min'
import dashboardRoutes from 'routes'
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Switch from '@material-ui/core/Switch';
import CardFooter from 'components/Card/CardFooter';
import { apiaddress } from 'auth/apiaddress';
import { postData } from 'auth/datapost';
const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};
const useStyles = makeStyles(styles);
function formatDate(inputDate) {
  const dateParts = inputDate.split('-');
  const year = dateParts[0];
  const month = new Date(inputDate + 'T00:00:00').toLocaleString('en-us', { month: 'long' });
  const day = dateParts[2];
  return `${day}-${month}-${year}`;
}

function Userlogs() {

const [logs,setlogs] = useState([{username:'',time:'',date:'',path:'',activity:''}])
const [users,setusers] = useState([])
const pickcol = (count) => {
  const pc = count % 2
  switch(pc){
    case(0):
    return 'rgba(0, 172, 193,0.5)'
    case(1):
    return 'white'
    default:
    return 'white'
  }
}
useEffect(()=>{
  const caller = async()=>{
  const data = await postData(apiaddress+'/get-users',{})
  setusers(data)
  }
  caller()
},[])

const handldlechange = async(e)=>{
  const retrievedlogs = await postData(apiaddress+'/read-logs',{date:formatDate(document.getElementById('date').value),username:document.getElementById('username').value})
  setlogs(retrievedlogs)
}

  return (
    <>
<div className='a11'>
<GridContainer justify="center" alignItems="center">

<GridItem xs={12} sm={3} md={3}> 
<p className='viewdepname'>ICT DEPARTMENT</p>
</GridItem>

<GridItem xs={12} sm={3} md={3}>
<input id='date' type="date" style={{width:'50%',marginLeft:'20%'}} className='viewdepname' onChange={handldlechange}/>
</GridItem>

<GridItem xs={12} sm={3} md={3}>
<select onChange={handldlechange} className='viewdepname' id="username">
{users.map((user)=>(
  <option value={user.employee_full_name}>{user.employee_full_name}</option>
))}
</select>
</GridItem>


</GridContainer>
</div>

<GridContainer justify="center" alignItems="center" spacing={1}>
<GridItem xs={12} sm={12} md={12}>   
<Card>
<CardBody>

<GridContainer justify="center" alignItems="center" spacing={1}>

<GridItem xs={12} sm={12} md={12}> 
<ul>
  <li style={{display:'block',backgroundColor:'red',color:'white'}}>
    <span className='logsshort'>USERNAME</span>
    <span className='logsshort'>DATE</span>
    <span className='logsshort'>TIME</span>
    <span className='logsmedium'>PATH</span>
    <span className='logslarge'>ACTIVITY</span>
  </li>
  {
    logs.map((log,count)=>(
    <li  style={{display:'block',backgroundColor:pickcol(count)}}>
    <span className='logsshort'>{log.username}</span>
    <span className='logsshort'>{log.date}</span>
    <span className='logsshort'>{log.time}</span>
    <span className='logsmedium'>{log.path}</span>
    <span className='logslarge'>{log.activity}</span>
    </li>
    ))
  }

</ul>
</GridItem>

</GridContainer>

</CardBody>
</Card>
</GridItem>
</GridContainer>

</>
  )
}
export default Userlogs